.wrapper {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.menu {
  padding: 10px 10px;
  background: rgba(217, 188, 247, 0.45);
  width: 250px;
  display: flex;
  color: white;
  flex-direction: column;
  border: 1px solid #ccc;
  border-radius: 4px;
}

input[type=text], select {
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}

button[type=button] {
  background-color: #4CAF50;
  color: white;
  padding: 14px 20px;
  margin: 8px 0;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

button[type=button]:disabled {
  background-color: gray;
}

button[type=button]:disabled:hover {
  background-color: gray;
}

button[type=button]:hover {
  background-color: #45a049;
}

.rooms {
  margin-left: 10px;
}

.room_wrapper {
  color: white;
  padding: 0 5px;
  width: 250px;
  display: flex;
  justify-content: space-between;
  background: rgba(217, 188, 247, 0.45);
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}

.room_info {
  margin: 8px 0;
  padding-left: 15px;
}
